body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.navigation {
  box-sizing: border-box;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  max-height: 60px;
  margin-right: 0;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.navigation-image img {
  margin: 10px 0;
  display: block;
}

.navigation-list {
  color: #000;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  list-style: none;
  display: flex;
}

.navigation-item {
  margin-left: 20px;
  position: relative;
}

.navigation-item:first-child {
  margin-left: 0;
}

.a-link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.a-link:hover, .is-active, .navigation_link:hover {
  color: #00acdc;
}

nav li:after {
  content: "";
  background-color: #00acdc;
  width: 0;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

nav li:hover:after {
  width: 100%;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

